<template>
  <v-card class="pa-4">
    <h2>Outstanding Assignment by Days (Branch)</h2>
    <v-row dense>
      <v-col cols="12" md="4">
        <v-select
          v-model="selectedDept"
          :items="deptOptions"
          item-text="name"
          item-value="id"
          label="Department"
          dense
          outlined
          required
        />
      </v-col>
      <v-col cols="12" md="4">
        <v-select
          v-model="selectedClass"
          :items="classOptions"
          item-text="subCode"
          item-value="id"
          label="Classification"
          dense
          outlined
          required
        />
      </v-col>
      <v-col cols="12" md="4">
        <v-menu v-model="menuMonth" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="selectedMonth"
              label="Month"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              required
            />
          </template>
          <v-date-picker
            v-model="selectedMonth"
            type="month"
            @change="menuMonth = false"
          />
        </v-menu>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" class="d-flex justify-end">
        <v-btn
          color="primary"
          @click="generateReport"
          :disabled="!selectedMonth"
        >
          Generate Report
        </v-btn>
        <v-btn
          color="success"
          class="ml-2"
          @click="exportExcel"
          :loading="exporting"
          :disabled="!reportData.length"
        >
          <v-icon left>mdi-file-excel</v-icon>
          Export to Excel
        </v-btn>
      </v-col>
    </v-row>
    <v-divider class="my-4" />
    <div v-if="reportData.length">
      <v-simple-table>
        <thead>
          <tr>
            <th>Branch</th>
            <th>Within TAT</th>
            <th>TAT Breach</th>
            <th>Total</th>
            <th>Breach %</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="row in reportData">
            <tr :key="row.branchCode">
              <td>
                <strong>{{ row.branchCode }}</strong>
              </td>
              <td>{{ row.withinTat }}</td>
              <td class="red--text font-weight-bold">{{ row.breachedTat }}</td>
              <td>{{ row.total }}</td>
              <td>
                <span :style="breachCellStyle(row.percent)" class="px-2 py-1">
                  {{ row.percent }}
                </span>
              </td>
            </tr>
            <tr
              v-for="detail in row.details"
              :key="row.branchCode + '-' + detail.dept + '-' + detail.class"
              class="grey--text text--darken-2"
            >
              <td>
                <span style="padding-left: 24px">
                  {{ row.branchCode }} ({{ detail.dept }} - {{ detail.class }})
                </span>
              </td>
              <td>{{ detail.withinTat }}</td>
              <td>{{ detail.breachedTat }}</td>
              <td>{{ detail.total }}</td>
              <td>
                <span
                  :style="breachCellStyle(detailPercent(detail))"
                  class="px-2 py-1"
                >
                  {{ detailPercent(detail) }}
                </span>
              </td>
            </tr>
          </template>
          <tr>
            <td><strong>TOTAL</strong></td>
            <td>
              <strong>{{ totals.withinTat }}</strong>
            </td>
            <td class="red--text font-weight-bold">
              <strong>{{ totals.breachedTat }}</strong>
            </td>
            <td>
              <strong>{{ totals.total }}</strong>
            </td>
            <td>
              <strong>{{ totals.breachPercent }}</strong>
            </td>
          </tr>
        </tbody>
      </v-simple-table>
    </div>
    <v-alert v-else-if="noDataMessage" type="warning" outlined class="mt-4">
      {{ noDataMessage }}
    </v-alert>
    <v-alert v-else type="info" outlined class="mt-4">
      Please select department, classification, and month, then click "Generate
      Report" to view data.
    </v-alert>
  </v-card>
</template>

<script>
import axios from "axios";
import moment from "moment";
import DeptDataService from "../../services/DeptDataService";
import SubDeptDataService from "../../services/SubDeptDataService";
import { apiConfig } from "../../config/api.config";

export default {
  name: "OutstandingBranch",
  data() {
    return {
      selectedDept: "all",
      deptOptions: [],
      selectedClass: "all",
      classOptions: [],
      selectedMonth: null,
      menuMonth: false,
      reportData: [],
      totals: {
        withinTat: 0,
        breachedTat: 0,
        total: 0,
        breachPercent: "0.00",
      },
      exporting: false,
      noDataMessage: "",
    };
  },
  methods: {
    async loadDepartments() {
      const res = await DeptDataService.getAll();
      this.deptOptions = [
        { id: "all", name: "ALL DEPARTMENTS" },
        ...res.data,
      ].sort((a, b) => a.name.localeCompare(b.name));
    },
    async loadClassifications() {
      const res = await SubDeptDataService.getAll();
      this.classOptions = [
        { id: "all", subCode: "ALL CLASSIFICATIONS", deptId: "all" },
        ...res.data,
      ].sort((a, b) => a.subCode.localeCompare(b.subCode));
    },
    filterClassifications(deptId) {
      if (deptId === "all") {
        this.filteredClassOptions = this.classOptions;
      } else {
        this.filteredClassOptions = this.classOptions
          .filter((c) => c.deptId === deptId || c.id === "all")
          .sort((a, b) => a.subCode.localeCompare(b.subCode));
      }
    },
    async generateReport() {
      this.reportData = [];
      this.totals = {
        withinTat: 0,
        breachedTat: 0,
        total: 0,
        breachPercent: "0.00",
      };
      this.noDataMessage = "";
      try {
        const params = {
          deptId: this.selectedDept,
          classificationId: this.selectedClass,
          month: moment(this.selectedMonth).format("YYYY-MM"),
        };
        const res = await axios.get(
          `${apiConfig.baseURL}/compliance/outstanding-branch`,
          { params }
        );
        this.processReportData(res.data);
        if (
          !Array.isArray(res.data.reportData) ||
          res.data.reportData.length === 0
        ) {
          const monthStr = moment(this.selectedMonth).format("MMMM YYYY");
          this.noDataMessage = `No outstanding data found for ${monthStr}.`;
        }
      } catch (err) {
        this.noDataMessage =
          err.message || "Failed to fetch report data. Please try again.";
      }
    },
    processReportData(data) {
      this.reportData = Array.isArray(data.reportData) ? data.reportData : [];
      let withinTat = 0,
        breachedTat = 0,
        total = 0;
      this.reportData.forEach((row) => {
        withinTat += row.withinTat;
        breachedTat += row.breachedTat;
        total += row.total;
      });
      this.totals.withinTat = withinTat;
      this.totals.breachedTat = breachedTat;
      this.totals.total = total;
      this.totals.breachPercent =
        total > 0 ? ((breachedTat / total) * 100).toFixed(2) : "0.00";
    },
    breachCellStyle(percent) {
      const val = parseFloat(percent);
      if (val > 80) {
        return "background:#FF6600;color:white;border-radius:4px;";
      } else if (val >= 50) {
        return "background:#FFB366;color:black;border-radius:4px;";
      }
      return "";
    },
    async exportExcel() {
      this.exporting = true;
      try {
        const params = {
          deptId: this.selectedDept,
          classificationId: this.selectedClass,
          month: moment(this.selectedMonth).format("YYYY-MM"),
        };
        const url =
          `${apiConfig.baseURL}/compliance/outstanding-branch/export?` +
          new URLSearchParams(params).toString();
        window.open(url, "_blank");
      } catch (err) {
        this.$emit("error", err.message || "Failed to export Excel");
      } finally {
        this.exporting = false;
      }
    },
    detailPercent(detail) {
      return detail.total > 0
        ? ((detail.breachedTat / detail.total) * 100).toFixed(2)
        : "0.00";
    },
  },
  mounted() {
    this.loadDepartments();
    this.loadClassifications();
  },
};
</script>
